html {
  min-height: 100%;
  position: relative;
}
body {
  background-image: linear-gradient(67deg, rgb(8, 48, 124), black);
  min-height: 100%;
}
html,
body {
  margin: 0;
  padding: 0;
}
root {
  height: 100%;
}
.all {
  height: 100%;
}
.main-head {
  grid-area: header;
}
.main-nav {
  grid-area: nav;
}
.main-info {
  grid-area: info;
}
.main-ui {
  grid-area: ui;
}
.main-chartTBC {
  grid-area: chartTBC;
}
.main-chartStats {
  grid-area: chartStats;
}
.main-footer {
  grid-area: footer;
}
#root {
  height: 100%;
}
.wrapper {
  display: grid;
  height: 100%;
  gap: 5px;
  grid-template-areas:
    'header'
    'nav'
    'info'
    'ui'
    'chartTBC'
    'chartStats'
    'footer';
}

@media screen and (min-width: 800px) {
  .wrapper {
    grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
    /*grid-template-rows: auto minmax(4em, 8vh) minmax(10em, 26vh) minmax(10em, 26vh) minmax(4em, 8vh);
    grid-template-rows: minmax(4em, 8vh) minmax(4em, 8vh) minmax(10em, 26vh) minmax(10em, 26vh);*/
    grid-template-rows: 4em 4em 10em 10em;
    grid-template-areas:
      'header   header'
      'nav      nav'
      'ui       chartTBC'
      'info     chartStats';
    width: 960px;
    margin: auto;
    margin-bottom: 100px;
  }
}

@media screen and (max-width: 799px) {
  .wrapper {
    grid-template-columns: 1fr;
    grid-template-areas:
      'header'
      'nav'
      'ui'
      'info'
      'chartStats'
      'chartTBC';
    margin-bottom: 100px;
  }
}
nav {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}
nav button {
  width: 100%;
}
.clickPad {
  width: 90%;
  height: 50%;
  margin: 10px;
}
.restartButton {
  width: 90%;
  height: 30%;
  margin: 10px;
}
h1 {
  text-align: center;
  color: white;
}

.main-info {
  color: white;
  text-align: center;
  font-size: 1.3em;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.main-info p {
  margin: 0;
}

.main-ui {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.cps {
  color: white;
  transition-duration: 200ms;
}
p {
  margin: 5%;
}
.clickPad {
  padding: 0;
  border: 0;
}

footer {
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100px;
  text-align: center;
}
